.input {
    display: flex;
    flex-flow: column;

    &__textbox {
        padding: 5px;
        //border-radius: 5px;
        color: black;
        border: 2px solid black;
        background: transparent;

        &:focus {
            outline: none;
        }
    }

    &__label {
        padding-left: 5px;
    }

    &__hint {
        font-size: 12px;
        color: grey;
        padding-left: 5px;
    }

    &_primary {
        color: $primary !important;

        .input__textbox {
            color: $primary;
            border: 2px solid $primary !important;
        }
    }

    &_accent {
        color: $accent !important;

        .input__textbox {
            color: $accent;
            border: 2px solid $accent !important;
        }
    }
}

.dark .input .input__textbox {
    color: white;
    border: 2px solid white;
}