.h1 {
    font-size: 30px;
    font-weight: 600;
}

.h2 {
    font-size: 26px;
    font-weight: 600;
}

.h3 {
    font-size: 22px;
    font-weight: 600;
}

.h4 {
    font-size: 18px;
    color: grey;
    font-weight: 600;
}

.h5 {
    font-size: 14px;
    color: grey;
    font-weight: 500;
}

.h6 {
    font-size: 10px;
    font-weight: 500;
}

.pr-val {
    &::after {
        content: "\"" + $primary + "\"";
    }
}

.ac-val {
    &::after {
        content: "\"" + $accent + "\"";
    }
}

.text {
    font-weight: 500;
}

.text_primary {
    color: $primary;
}

.text_accent {
    color: $accent;
}
